body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    position: relative;
}

.app {
    position: fixed;
    width: 100%;
    height: 100%;
}

h3, h4 {
    margin: 0;
}

h4 {
    margin-bottom: 10px;
}

h3 {
    margin-bottom: 15px;
}

@font-face {
    font-family: "Rubik-Regular";
    src: local('Rubik-Regular'), url("./fonts/Rubik-Regular.ttf");
}

* {
    font-family: Rubik-Regular;
}